import React from "react";
import { BrowserRouter as Router } from "react-router-dom";
import { HelmetProvider } from "react-helmet-async";
import Routes from './routes';
import './styles/main.scss';

const App: React.FC = () => (
  <Router>
    <HelmetProvider>
      <Routes />
    </HelmetProvider>
  </Router>
);

export default App;
